import React from 'react';
import { Route, Routes } from 'react-router-dom';

const PersonalDetails = React.lazy(() => import('./PersonalDetails/PersonalDetails'));
const PersonAddress = React.lazy(() => import('./PersonAddress/PersonAddress'));
const Idv = React.lazy(() => import('./Idv/Idv'));
const KycReview = React.lazy(() => import('./Review/Review'));
const Declined = React.lazy(() => import('./Declined/Declined'));
const Success = React.lazy(() => import('./Success/Success'));

/**
 * KYC Routes
 * @returns {JSX.Element}
 */
function KycRouter() {
  return (
    <Routes>
      <Route path="personalDetails" element={<PersonalDetails />} />
      <Route path="personAddress" element={<PersonAddress />} />
      <Route path="idv" element={<Idv />} />
      {/* <Route path="idvCallback" element={<IdvCallback />} /> */}
      <Route path="review" element={<KycReview />} />
      <Route path="success" element={<Success />} />
      <Route path="declined" element={<Declined />} />
    </Routes>
  );
}

export default KycRouter;
